import React from "react";

//Loader
import BeatLoader from "react-spinners/BeatLoader";
//transition
import { Transition } from "react-transition-group";
//transition style
const transitionStyles = {
  entering: { opacity: 1 },
  entered: { opacity: 1 },
  exiting: { opacity: 0 },
  exited: { opacity: 0 },
};

const duration = 100;

const defaultStyle = {
  transition: `opacity 0.2s`,
  opacity: 0,
  zIndex: 99999,
  pointerEvents: "none",
};

const Loader = (props) => {
  return (
    <Transition in={props.isLoading} timeout={100} unmountOnExit={false}>
      {(state) => (
        <div
          className="loader"
          style={{
            zIndex: "500",
            ...defaultStyle,

            ...transitionStyles[state],
          }}
        >
          <BeatLoader />
        </div>
      )}
    </Transition>
  );
};

export default Loader;
